// footer de main //

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid $color-texto;
}

@media all and (min-width: 768px) {
  .footer {
    position: relative;
  }
}

@media all and (min-width: 1200px) {
  .footer {
    position: relative;
  }
}

.titlefooter {
  margin-top: 30px;
}

@media all and (min-width: 768px) {
  .titlefooter {
    position: absolute;
    left: 45px;
  }
}

.listfooter {
  text-decoration: none;
  list-style: none;
  padding: 0;
  text-align: center;
  line-height: 30px;
}

@media all and (min-width: 768px) {
  .listfooter {
    position: absolute;
    right: 45px;
    text-align: right;
    margin-top: 25px;
  }
}

.listfooter a {
  text-decoration: none;
  color: $color-texto;
  &:hover{
    color: $color-header2;
  }
}

.list li {
  margin-bottom: 6px;
}

.image-footer {
  width: 116px;
  height: 45px;

  &:hover {
    animation-name: logoAnimation;
    animation-duration: 5s;
    animation-iteration-count: 1;
  }
}

@media all and (min-width: 768px) {
  .image-footer {
    position: absolute;
    left: 45px;
    top: 55px;
    margin-bottom: 60px;
  }
}
